import Link from "next/link";
import TertiaryHeader from "@/components/Texts/TertiaryHeader";
import UtilityButton from "@/components/Buttons/UtilityButton";
import DangerButton from "@/components/Buttons/DangerButton";

export default function TableList() {
  return (
    <div className="w-full overflow-x-auto">
      <table className="min-w-full bg-white rounded-lg shadow-md">
        <thead className="text-[#013300] bg-green-50 border-b border-gray-200">
          <tr>
            <th className="px-4 py-2 text-left"><TertiaryHeader title="No#" /></th>
            <th className="px-4 py-2 text-left"><TertiaryHeader title="Title" /></th>
            <th className="px-4 py-2 text-left"><TertiaryHeader title="Phonemic Level" /></th>
            <th className="px-4 py-2 text-left"><TertiaryHeader title="Date to Use" /></th>
            <th className="px-4 py-2 text-left"><TertiaryHeader title="Status" /></th>
            <th className="px-4 py-2 text-left"><TertiaryHeader title="Actions" /></th>
          </tr>
        </thead>
        <tbody className="text-gray-700">
          <tr className="border-b border-gray-200 hover:bg-green-50 transition-colors duration-150">
            <td className="px-4 py-2">1</td>
            <td className="px-4 py-2">Vowels</td>
            <td className="px-4 py-2">Non-Reader</td>
            <td className="px-4 py-2">Date to Use</td>
            <td className="px-4 py-2">Completed</td>
            <td className="px-4 py-2 flex gap-2">
              <Link href="remedial/Flashcards" className="flex items-center gap-1">
                <UtilityButton small className="flex items-center gap-1">
                  <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round" className="lucide lucide-play-icon lucide-play"><polygon points="6 3 20 12 6 21 6 3"/></svg>
                  Play
                </UtilityButton>
              </Link>
              <DangerButton small>
                Delete
              </DangerButton>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
